.HomePage {
  .loading {
    font-size: 2rem;
  }
  .pagination {
    padding: 0;
    li {
      margin: 0 2px;
      display: inline-block;
      a {
        color: #666; 
        padding: 5px 10px;
        display: inline-block;
        text-decoration: none;
        border: 1px solid #eee;
        &:hover {
          color: #000;
          text-decoration: underline;
          background-color: #f5f5f5;
        }
      }
    }
  }
}